    let friends = document.getElementById('friends'),
        pagination = document.getElementById('pagination'),
        sliderWrapper = document.getElementById('sliderWrapper'),
        modal = document.getElementById('modal'),
        modalContent = document.getElementById('modalContent'),
        modalClose = document.getElementById('modalClose')

    let paginIndex = 1,
        objects = 8,
        dataAmount,
        paginAmount

    fetch('json/db.json')
        .then(res => res.json())
        .then(data => {

            // Creating card blocks in SwipeSlider
            if (window.location.href.indexOf('pets') === -1) {
                for (let i = 1; i <= 6; i++) {
                    mySwiper.appendSlide([`
                    <div class="swiper-slide card">
                        <div class="card__img-wrap"><img src="${data[i].photo}" alt="Pet" class="card__img"></div>
                        <span class="card__name">${data[i].name}</span>
                        <button class="btn btn--primary card__btn" data-id="${i}">Learn More</button>
                    </div>
                `]);
                }
                listenTheBtns()
            }

            // Creating pagination
            if (window.location.href.indexOf('pets') > -1) {
                dataAmount = data.length;
                paginAmount = Math.ceil(dataAmount / objects)
                for (let i = 1; i <= paginAmount; i++) {
                    pagination.innerHTML += `
                <button class="pagination__item" data-id="${i}">${i}</button>`
                }

                showActiveList(paginIndex)

                let paginNum = document.querySelectorAll('.pagination__item')
                paginNum[paginIndex - 1].classList.add('active')

                paginNum.forEach(function (el) {
                    el.addEventListener('click', function (e) {

                        paginNum.forEach((elem) => {
                            elem.classList.remove('active')
                        })
                        paginIndex = Number(e.toElement.dataset.id);

                        friends.innerHTML = ''
                        showActiveList(paginIndex)
                        paginNum[paginIndex - 1].classList.add('active')
                        window.scrollTo(0, 0)
                    })
                })
            }

            function showActiveList(paginIndex) {
                let lastIndex = (paginIndex * objects) - 1;
                let firstIndex = (lastIndex - (objects - 1));

                if (lastIndex > data.length) {
                    lastIndex = data.length - 1;
                    firstIndex = (lastIndex -
                        (data.length -
                            (objects * Math.floor(data.length / objects)))) + 1;
                }

                if (window.location.href.indexOf('pets') > -1) {
                    for (let i = firstIndex; i <= lastIndex; i++) {
                        friends.innerHTML += `
                            <div class="card">
                                <div class="card__img-wrap"><img src="${data[i].photo}" alt="Pet" class="card__img"></div>
                                <span class="card__name">${data[i].name}</span>
                                <button class="btn btn--primary card__btn" data-id="${i}">Learn More</button>
                            </div>`
                    }
                }
                listenTheBtns()
            }

            function listenTheBtns() {
                let cardBtns = document.querySelectorAll('.card__btn');
                cardBtns.forEach(el => {
                    el.addEventListener('click', function (e) {
                        console.log(e.target.dataset.id)
                        let index = e.target.dataset.id;
                        createModal(index);
                        modal.classList.add('opened')
                    })
                });
            }

            function createModal(index) {
                modalContent.innerHTML = `
                    <div class="modal__wrap">
                        <img src="${data[index].photo}" alt="" class="modal__img" />
                    </div>
                    <div class="modal__info">
                        <h3 class="modal__name">${data[index].name}</h3>
                        <span class="modal__breed">${data[index].breed}</span>
                        <p class="modal__description">${data[index].description}</p>
                        <ul class="modal__list">
                            <li class="modal__item">
                            <span class="modal__data">Age: ${data[index].additional.age}</span>
                            </li>
                            <li class="modal__item">
                            Inoculations: ${data[index].additional.inoculations}
                            </li>
                            <li class="modal__item">
                            Diseases: ${data[index].additional.diseases}
                            </li>
                            <li class="modal__item">
                            Parasites: ${data[index].additional.parasites}
                            </li>
                        </ul>
                    </div>`
                modal.classList.add('opened')
            }

            modalClose.addEventListener('click', function () {
                modal.classList.remove('opened')
            });

        });
let header = document.getElementById('header');


function colorHeader() {
  if (pageYOffset >= 100) {
    header.style.backgroundColor = "rgba(56, 44, 39, 0.9)";
    header.style.paddingTop = '10px'
  } else {
    header.style.backgroundColor = "transparent";
    header.style.paddingTop = '45px'
  }
}

if (window.location.href.indexOf('pets') === -1) {
  window.onload = function () {
    colorHeader()
  }
  window.addEventListener("scroll", function () {
    colorHeader()
  });
}
var mySwiper = new Swiper('.swiper-container', {
  loop: true,
  spaceBetween: 50,
  slidesPerView: 3,
  preventClicks: false,
  preventClicksPropagation: false,

  navigation: {
    nextEl: '.slider__arrow--right',
    prevEl: '.slider__arrow--left',
  },
})